import React, {useEffect, useState} from 'react';
import CIcon from "@coreui/icons-react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import MaterialTable from "material-table";
import Select from "react-select";
import makeAnimated from "react-select/animated";
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CInput,
  CLabel,
  CRow
} from "@coreui/react";

import {
  GetApplSetingsAction,
  setFormSettingsAction,
  AddApplSettingAction,
  GetApplSetingByCodeAction,
  UpdateApplSettingAction,
  DeleteApplSettingAction,
} from "src/common/redux/action";
import { useDispatch, useSelector } from 'react-redux';

import imageNoAction from "../../images/vektors/house.png";

const Settings = () => {
  /**
   * setting reducers
   */
  const dispatch = useDispatch();
  const { SetApplSetingsReducer, SetFormSettingReducer,setFormSettings } = useSelector(
    (state) => state.settingsReducer
  );

  const { code, name, valueString } = SetFormSettingReducer;

  /**
   * setting state local
   */
  const [formShowHide, setFormHide] = useState(false);
  const [stateUpdate, setStateUpdate] = useState(false);
  const startLoad = () => {
    dispatch(GetApplSetingsAction("GET_LIST_APPL_SETTINGS"));
  }

  // open form setting
  const handleFormSettings = (type, code)=> {
     if(type ===  "add"){
        setFormHide(true);
        setStateUpdate(false);
     }else{
        dispatch(GetApplSetingByCodeAction(code));
        setFormHide(true);
        setStateUpdate(true);
     }
  }

  const prosesAddSetting = () => {
    dispatch(AddApplSettingAction(SetFormSettingReducer));
  }
  const prosesEditSetting = () => {
     dispatch(UpdateApplSettingAction(SetFormSettingReducer));
  }

  const handleProsesDelete = (code) => {
    dispatch(DeleteApplSettingAction(code));
  }

  const columns = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row d-flex justify-content-center">
            <button type="button" className="btn btn-sm btn-dark text-center " onClick={() => handleFormSettings("edit",rowData.code)}>
              <CIcon name="cil-pencil" /> Update
            </button>

            <button type="button" className="btn btn-sm btn-danger" onClick={() =>handleProsesDelete(rowData.code)}>
              <CIcon name="cil-drop" /> Hapus
            </button>
          </div>
        );
      },
    },
    { title: "Code", field: "code" },
    { title: "Nama", field: "name" },
    { title: "Value", field: "valueString" },
  ];


  useEffect(() => {
    startLoad()
  }, [useDispatch]);
  return (
    <div>
      <CRow>
        <CCol md="7" xs="7">
          <CCard>
            <CCardHeader>
              <h4 color="info" md="3">
                {" "}
                Tabel Pengaturan Aplikasi
              </h4>
              <div className="text-right">
                <CButton
                  color="primary"
                  onClick={() => handleFormSettings("add")}
                >
                  {" "}
                  <FontAwesomeIcon className="mr-1" icon="plus" />
                  Tambah{" "}
                </CButton>
              </div>
            </CCardHeader>
            <CCardBody>
              <MaterialTable
                title=""
                options={{
                  headerStyle: {
                    backgroundColor: "#01579b",
                    color: "#FFF",
                  },
                  filtering: true,
                }}
                columns={columns}
                data={SetApplSetingsReducer ? SetApplSetingsReducer : []}
              />
            </CCardBody>
          </CCard>
        </CCol>
        <CCol md="5" xs="5">
          {formShowHide === false ? (
            <div>
              <CCard>
                <CCardBody className="text-center">
                  <img
                    src={imageNoAction}
                    className="mb-2"
                    width="100%"
                    height="100%"
                  />
                </CCardBody>
              </CCard>
            </div>
          ) : (
            <div>
              <CCard>
                <CCardHeader>
                  <h4>
                    {stateUpdate
                      ? "Form Edit Pengaturan Aplikasi"
                      : "Form Add Pengaturan Aplikasi"}
                  </h4>
                </CCardHeader>
                <CCardBody>
                  <CForm>
                    <CFormGroup className="form-row">
                      <CLabel htmlFor="nf-code" className="col-md-4">
                        {" "}
                        Code
                      </CLabel>
                      <div className="col-md-8">
                        <CInput type="text" value={code} placeholder="Input Code ..." onChange={(event) => dispatch(setFormSettingsAction("code",event.target.value))} />
                      </div>
                    </CFormGroup>
                    <CFormGroup className="form-row">
                      <CLabel htmlFor="nf-name" className="col-md-4">
                        {" "}
                        Nama{" "}
                      </CLabel>
                      <div className="col-md-8">
                        <CInput
                          value={name}
                          placeholder="Input Nama ..."
                          onChange={(event) => dispatch(setFormSettingsAction("name", event.target.value))}
                        />
                      </div>
                    </CFormGroup>
                    <CFormGroup className="form-row">
                      <CLabel htmlFor="nf-name" className="col-md-4">
                        {" "}
                        Value{" "}
                      </CLabel>
                      <div className="col-md-8">
                        <CInput value={valueString} placeholder="Input Value String ..." onChange={(event) => dispatch(setFormSettingsAction("valueString", event.target.value))}  />
                      </div>
                    </CFormGroup>
                    <CButton
                      color={stateUpdate ? "warning" : "success"}
                      className="col-md-12"
                      onClick={stateUpdate ? prosesEditSetting : prosesAddSetting}
                    >
                      {stateUpdate ? "Simpan Perubahan " : "Simpan Tambah"}
                    </CButton>
                  </CForm>
                </CCardBody>
              </CCard>
            </div>
          )}
        </CCol>
      </CRow>
    </div>
  );
}

export default Settings;
